/* --------- Player Card ----------*/
.baseball_card{
    position: relative;
    min-height: 350px;
    max-height:350px;
    min-width:242px;
    border: 3px black solid;
    align-items: center;
    top:0;
    background-color: #F6F7DD;
    justify-content: center;
}
.batter_name{
    font-size:24px;
    text-align: center;
    flex:1;
    font-family: 'MLBBLOCK';
   
}
.batter_image{
    position: relative;
    left:20px;
    top:40px;
    z-index: 1;
    width:200px;
    height:200px;
    border: 2px black solid; 
    margin-bottom: 50px;
}
.team_image{
    position: absolute;
    top: 30px;
    left:8px;
    height:60px;
    width:60px;
    z-index: 3;
}

/* ---------- Mobile ----------- */
@media only screen and (max-width: 600px){
 
    /* ------- Player Card Mobile--------*/
    .baseball_card{
        min-width:242px;
        max-width: 242px;
    }
    
}